
.variable-update-beautifier {
  width: 100%;
  margin: 0;
  padding: 0;
}

.update-container {
  margin: 1rem 0;
}

.update-card {
  background: linear-gradient(135deg, rgba(99, 102, 241, 0.1) 0%, rgba(236, 72, 153, 0.1) 100%);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 0.75rem;
  overflow: hidden;
  backdrop-filter: blur(20px) saturate(180%);
  box-shadow:
    0 4px 16px rgba(0, 0, 0, 0.2),
    0 2px 4px rgba(99, 102, 241, 0.15),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);

  background-image:
    radial-gradient(circle at 20% 30%, rgba(99, 102, 241, 0.15) 0%, transparent 50%),
    radial-gradient(circle at 80% 70%, rgba(236, 72, 153, 0.15) 0%, transparent 50%);

  &.is-expanded {
    border-color: rgba(236, 72, 153, 0.3);
    box-shadow:
      0 8px 24px rgba(0, 0, 0, 0.3),
      0 4px 8px rgba(99, 102, 241, 0.2),
      inset 0 1px 0 rgba(255, 255, 255, 0.15);
  }
}

.update-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 1.25rem;
  cursor: pointer;
  user-select: none;
  transition: all 0.2s ease;

  &:hover {
    background: rgba(255, 255, 255, 0.05);
  }

  .header-left {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    flex: 1;
  }

  .expand-icon {
    width: 1.5rem;
    height: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #6366f1;
    transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);

    &.rotated {
      transform: rotate(180deg);
    }
  }

  .header-title {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    color: #e5e7eb;
    font-weight: 600;
    font-size: 1rem;

    i {
      color: #6366f1;
    }
  }

  .header-badge {
    width: 2rem;
    height: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(135deg, rgba(99, 102, 241, 0.3) 0%, rgba(236, 72, 153, 0.3) 100%);
    border-radius: 50%;
    color: #6366f1;
    font-size: 0.9rem;
  }
}

.update-content {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  padding: 1.5rem;
  animation: slideDown 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.content-wrapper {
  color: #e5e7eb;
  font-size: 0.95rem;
  line-height: 1.7;

  :deep(.analysis-section),
  :deep(.jsonpatch-section) {
    margin-bottom: 1.5rem;

    &:last-child {
      margin-bottom: 0;
    }
  }

  :deep(.section-header) {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    margin-bottom: 0.75rem;
    padding-bottom: 0.5rem;
    border-bottom: 2px solid rgba(99, 102, 241, 0.3);
    color: #ec4899;
    font-weight: 600;
    font-size: 1rem;

    i {
      color: #6366f1;
    }
  }

  :deep(.section-content) {
    color: #d1d5db;
  }

  :deep(.analysis-item) {
    margin: 0.1rem 0;
    padding-left: 1rem;
    color: #e5e7eb;
    line-height: 1;
  }

  :deep(.analysis-subitem) {
    margin: 0.15rem 0;
    padding-left: 2rem;
    color: #cbd5e1;
    font-size: 0.9em;
    line-height: 1.4;
  }

  :deep(.json-code) {
    margin: 0.75rem 0;
    padding: 1rem;
    background: rgba(0, 0, 0, 0.3);
    border-radius: 0.5rem;
    overflow-x: auto;
    border: 1px solid rgba(255, 255, 255, 0.1);
    font-family: 'Fira Code', 'Courier New', 'Consolas', monospace;
    font-size: 0.85rem;
    line-height: 1.6;

    .json-key {
      color: #6366f1;
      font-weight: 600;
    }

    .json-string {
      color: #10b981;
    }

    .json-number {
      color: #f59e0b;
    }

    .json-boolean {
      color: #ec4899;
    }

    .json-null {
      color: #9ca3af;
    }

    .json-op {
      color: #8b5cf6;
      font-weight: 600;
    }
  }

  :deep(.json-error) {
    margin: 0.75rem 0;
    padding: 1rem;
    background: rgba(239, 68, 68, 0.1);
    border: 1px solid rgba(239, 68, 68, 0.3);
    border-radius: 0.5rem;
    color: #fca5a5;
    font-family: 'Courier New', monospace;
    font-size: 0.85rem;
    white-space: pre-wrap;
  }
}

.no-update {
  padding: 2rem;
  text-align: center;
  color: #9ca3af;
  font-size: 0.9rem;

  p {
    margin: 0;
  }
}
